import React from 'react';
import Quagga from 'quagga'
import Webcam from "react-webcam";

export default class Scanner extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            codigo: false
        }

        this.getScreenshot = this.getScreenshot.bind(this)
    }

    setRef = webcam => {
        this.webcam = webcam;
    };

    scanImage(dataUri) {
        Quagga.decodeSingle({
            decoder: {
                readers: ["ean_reader"],
            },
            locate: true,
            src: dataUri
        }, function(result) {
            if(result.codeResult) {
                this.setState({
                    codigo: result.codeResult.code
                });
            } else {
                this.setState({
                    error: "Error de lectura"
                });
            }
        }.bind(this));
    }

    getScreenshot() {
        if (this.webcam.getScreenshot()) {
            this.scanImage(this.webcam.getScreenshot())
        } else {
            this.setState({
                error: "No se puede capturar"
            });
        }
    }

    render() {
        return (
            <div className="container text-center">
                <h2>Escanear productos</h2>
                {this.state.error &&
                    <div className="alert alert-danger" role="alert">{this.state.error}</div>
                }
                {this.state.codigo &&
                    <h4>CÃ³digo: {this.state.codigo}</h4>
                }
                <Webcam
                    style={{backgroundColor: '#333', width: '100%', height: '370px', borderRadius: '20px'}}
                    audio={false}
                    ref={this.setRef}
                    screenshotFormat="image/jpeg"
                    videoConstraints={{
                        width: 640,
                        height: 480,
                        facingMode: { exact: "environment" }
                    }}
                />
                <br />
                <div className="clearfix"></div>
                <button type="button" className="btn btn-primary" onClick={this.getScreenshot}>Escanear</button>
            </div>
        );
    }
}
